%logo-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;

  > * {
    @include set-margin(1);

    img, picture {
      @include spacing('height',2);
      width: auto; } } }

%logo-grid-neutral {
  @extend %logo-grid;

  figure {
    @include figure-neutral-wash(); } }
