.cards {
  @include bp(med) {
    @include separated-row(2,0); }

  @include bp(lrg) {
    @include separated-row(3,0); }

  div.image {
    @include square(100%);

    &:after {
      content: '';
      position: absolute;
      height: 100%;
      width: 100%;
      background-color: $clr1;
      mix-blend-mode: color;
      top: 0;
      cursor-events: none; }


    img {
      filter: unquote("grayscale()") /* Workaround for conflict with libsass and filter */; }

    &:hover {
      img {
        filter: none; }

      &:after {
        display: none; } } }

  h3 {
    &:before {
      display: none; } } }


.position {
  font-family: $headingfont;
  font-style: italic;
  @include font-size(1);
  @include line-height(1); }

.bio {
  @include set-top-margin(0); }

ul.links {
  margin: 0;
  padding: 0;
  @include image-bullets(url(#{$base_url}/assets/img/icons/link.svg),-2,-2,-2,0);

  a {
    text-decoration: underline;

    &:hover {
      color: $impact; } } }
