h1 {
  font-weight: $lightfw;
  text-align: center;
  width: 90%;
  padding-bottom: rv(3);
  margin-bottom: rv(3);
  border-bottom: 4px double $black;


  @include bp(med) {
    width: 70%; } }

main {
  line-height: $blh * 1.5;

  > * {
    margin-left: auto;
    margin-right: auto; }

  > p, h1, h2, h3, h4, h5, h6, ul, ol, li {
    max-width: $legible_inset_width;
    line-height: inherit; } }


.fontfamily-base {
  font-family: $basefont; }

.fontfamily-heading {
  font-family: $headingfont; }

.fontfamily-alt {
  font-family: $altfont; }


$h2_ornament_width: 300px;
$h3_ornament_width: 200px;


h2 {
  font-style: italic;
  width: 100%;
  text-align: center; }

main {
  h2 {
    border-bottom: 2px solid $black;
    padding-bottom: rv(1);
    margin: rv(3) auto rv(1) auto;
    position: relative;

    &:after {
      content: '';
      z-index: -1;
      position: absolute;
      box-sizing: border-box;
      height: $h2_ornament_width;
      width: $h2_ornament_width;
      top: $h2_ornament_width * -0.125;
      right: $h2_ornament_width * -0.5;
      border-radius: $h2_ornament_width/2;
      background-color: set-colour(1,'watermark'); } }

  h1 + h2 {
    border-bottom: 0;
    margin-bottom: 0;

    &:after {
      display: none; } } }

h3 {
  font-family: $altfont;
  text-transform: uppercase;
  font-weight: $semiboldfw; }

main {
  h3:nth-of-type(2n) {
    position: relative;

    &:before {
      z-index: -1;
      content: '';
      position: absolute;
      box-sizing: border-box;
      height: $h3_ornament_width;
      width: $h3_ornament_width;
      top: $h3_ornament_width * -0.25;
      left: $h3_ornament_width * -0.5;
      border-radius: $h3_ornament_width/2;
      background-color: set-colour(1,'watermark'); } } }

h4 {
  font-family: $altfont;
  font-weight: $normalfw; }

h5 {
  font-family: $basefont;
  font-weight: $boldfw;
  text-transform: uppercase; }

h6 {
  font-family: $basefont;
  font-style: italic; }
