// divider line
@mixin divider-line($border-width: 1px, $border-style: solid, $border-color: $black, $padfactor: 2) {
  width: 100%;
  display: block;
  border-bottom: $border-width $border-style $border-color;
  @include spacing('padding-top',$padfactor);
  @include spacing('padding-bottom',$padfactor); }
