// Local paths for nav buttons

.prev {
  background: url("#{$base_url}/assets/img/viewer/prev_btn.svg") no-repeat; }

.prev:hover {
  background: url("#{$base_url}/assets/img/viewer/prev_btn_over.svg") no-repeat; }

.prev:disabled {
  background: url("#{$base_url}/assets/img/viewer/prev_btn_inactive.svg") no-repeat; }

.next {
  background: url("#{$base_url}/assets/img/viewer/next_btn.svg") no-repeat; }

.next:hover {
  background: url("#{$base_url}/assets/img/viewer/next_btn_over.svg") no-repeat; }

.next:disabled {
  background: url("#{$base_url}/assets/img/viewer/next_btn_inactive.svg") no-repeat; }
